<template>
  <el-row class="home white-back">
    <el-row type="flex" align="middle">
      <icon-svg
          style="height: 112px;width: 112px"
          name="100-sunny"/>
      <h2>美文欣赏</h2>
    </el-row>
    <el-row v-loading="loading">
      <h3 class="text-center">{{newArticle.title}}</h3>
      <el-row class="content" v-html="newArticle.content"></el-row>
      <el-row type="flex" style="flex-direction: column;" justify="space-around" align="bottom">
        <el-row type="flex" align="middle">
          <span>作者: </span>
          <el-link type="success"
                   :href="linkTo(newArticle.author)"
                   target="_blank">
            <span class="font-size--medium">{{newArticle.author}}</span>
          </el-link>
        </el-row>
        <el-row class="ptb-10" v-if="newArticle.date">{{newArticle.date.curr}}</el-row>
      </el-row>
    </el-row>
    <el-row v-for="(article, index) in articles" :key="index">
      <h3 class="text-center">{{article.title}}</h3>
      <el-row class="content" v-html="article.content"></el-row>
      <el-row type="flex" style="flex-direction: column;" justify="space-around" align="bottom">
        <el-row type="flex" align="middle">
          <span>作者: </span>
          <el-link type="success"
                   :href="linkTo(article.author)"
                   target="_blank">
            <span class="font-size--medium">{{article.author}}</span>
          </el-link>
        </el-row>
        <el-row class="ptb-10">{{article.date.curr}}</el-row>
      </el-row>
    </el-row>
    <el-row class="mt-10 text-center">
      <el-button @click="readMore">下一篇</el-button>
    </el-row>
  </el-row>
</template>
<script>
import { getArticleDate, getArticleToday, getArticleRandom } from '@/api/oneArticle'

export default {
  name: 'home',
  data () {
    return {
      noData: false,
      loading: false,
      newArticle: {},
      articles: []
    }
  },
  mounted () {
    this.getArticle()
  },
  computed: {},
  methods: {
    init () {// 滚动
      let _this = this
      window.onmousewheel = function (e) {
        if (e.wheelDelta < 0) {// 固定值120 向上滚为-120 向下是120
          _this.$refs.articleScroller && (_this.$refs.articleScroller.scrollBy(0, 30, false))//避免ref中存在scrollerBottom
        } else {
          _this.$refs.articleScroller && (_this.$refs.articleScroller.scrollBy(0, -30, false))
        }
      }
    },
    getArticle () {
      this.loading = true
      getArticleToday({
        param: {}
      }).then(res => {
        this.loading = false
        this.newArticle = res.data
      }).catch(err => {
        console.log('getArticleDate err', err)
      })
    },
    linkTo (val) {
      return `http://www.baidu.com/s?wd=${val}`
    },
    readMore () {
      getArticleRandom({
        param: {}
      }).then(res => {
        if (res.data) {
          this.articles.push(res.data)
          this.noData = true
        } else {
          this.noData = false
        }
      }).catch(err => {
        this.noData = false
        console.log('getArticleRandom err', err)
      })
    },
    getOneDayArticle () {
      getArticleDate({
        param: {
          date: '20190425'
        }
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log('getArticleDate err', err)
      })
    }
  }
}
</script>
<style lang="scss">
.home {
  .topTitle {
    display: flex;
    justify-content: space-around;

    span {
      margin: 10px;
      width: 40px;
    }
  }

  .content {
    padding: 10px 40px;

    p {
      text-indent: 2em;
    }
  }

  .readMore {
    height: 80px;
  }
}

@media screen and (max-device-width: 400px) {
  // 真实宽度小于400px (非web端 窗口大小)
  .topTitle {
    flex-wrap: wrap;
  }
}
</style>
