// import layoutMain from '@/layout/main'
import layoutHeader from '@/layout/header'
import home from '@/views/article'

const meta = {
  requiresAuth: true
}

/**
 * 在主框架之内显示
 */
const frameIn = [{
  path: '/home',
  // redirect: '/home',
  meta,
  component: layoutHeader,
  children: [{
    path: '/home',
    name: 'home',
    meta,
    component: home,
  }, {
    path: '/video',
    name: 'video',
    meta,
    component: () => import('@/views/video-parsing'),
    // children: (pre => [])('video-')
  }, {
    path: '/video-play',
    name: 'play',
    component: () =>
      import('@/views/video-parsing/play')
  }, {
    path: '/help',
    name: 'help',
    meta,
    component: () => import('@/views/help')
  }, {
    path: '/joke',
    name: 'joke',
    meta,
    component: () => import('@/views/joke'),
    // children: (pre => [])('joke-')
  }, {
    path: '/word-clock',
    name: 'word-clock',
    meta,
    component: () => import('@/views/word-clock'),
    // children: (pre => [])('joke-')
  }, {
    path: '/author',
    name: 'author',
    meta,
    component: () => import('@/views/author'),
  },

    //news
    {
      path: '/news',
      name: 'news',
      component: () =>
        import('@/views/news')
    }, {
      path: '/setting',
      name: 'setting',
      meta,
      component: () => import('@/views/setting'),
    }]
}]
/**
 * 在主框架之外显示
 */
const frameOut = [
  {
    path: '/',
    redirect: '/index'
  },  // index
  {
    path: '/index',
    name: 'index',
    component: () =>
      import('@/views/index')
  },
  // 页面重定向使用 必须保留
  {
    path: '/redirect/:path*',
    component: {
      beforeCreate () {
        this.$router.replace(JSON.parse(this.$route.params.path))
      },
      render: h => h()
    }
  },
  // 页面刷新 必须保留
  {
    path: '/refresh',
    name: 'refresh',
    component: {
      beforeRouteEnter (to, from, next) {
        next(vm => {
          vm.$router.replace(from.fullPath)
        })
      },
      render: h => h()
    }
  },
  {
    path: '/learn',
    name: 'learn',
    component: () =>
      import('@/views/learn')
  }, {
    path: '/daily',
    name: 'daily',
    component: () =>
      import('@/views/daily')
  },
  // 个人主页
  {
    path: '/myself',
    name: 'myself',
    component: () =>
      import('@/views/myself')
  }]

// 重新组织后导出
export default [
  ...frameIn,
  ...frameOut
]
