import Vue from 'vue'
import App from './App.vue'
// 基本插件
import router from './router'
import store from './store'

// 数据持久化
import util from '@/libs/util.js'

// 语言插件
import i18n from './lang'
// 基本UI库
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

// svg 图标
import '@/assets/svg-icons'

// 组件
import '@/components'

let size = util.db.get('sys.size.value').value()
Vue.use(ElementUI, { size: size || 'small' })

// <--- 效果插件 --->
// 图片预览 随意拖动放大缩小
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'

Vue.use(Viewer, {
  defaultOptions: {
    zIndex: 9999
  }
})
// css3过渡效果
import animated from 'animate.css'

Vue.use(animated)

// 上拉加载 下拉刷新
//https://www.jianshu.com/p/a39f5276ff0b
import VueScroller from 'vue-scroller'

Vue.use(VueScroller)

// jsonp
import VueJsonp from 'vue-jsonp'

Vue.use(VueJsonp)

// If you want to setup the global timeout, just:
Vue.use(VueJsonp, 5000)


Vue.config.productionTip = false

new Vue({
  router,
  store,
  i18n,
  render: h => h(App),
  mounted () {
    let a = window.location.href.split(':')[0]
    const token = util.cookies.get(a + '-token')
    if (token) {
      this.$store.dispatch('load/load')
    } else {
      this.$router.push({ name: 'index' })
    }
  },
  destroyed () {
    util.cookies.remove('token')
  }
}).$mount('#app')
